.sidebar {
  width: 200px;
  background-color: #d3d3d3;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 10px;
  box-sizing: border-box;
  border-right: 1px solid #bbb;
  font-family: 'Inter', sans-serif;
  box-shadow: 2px 0 5px rgba(0,0,0,0.1);
}

.sidebar nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.sidebar nav ul ul {
  padding-left: 15px;
}

.sidebar-button, .sidebar a {
  display: block;
  background-color: #d3d3d3;
  border: none;
  padding: 8px 0px 12px 8px;
  width: 100%;
  text-align: left;
  cursor: pointer;
  font-size: 14px;
  color: #000;
  text-decoration: none;
  border-radius: 4px;
  margin-bottom: 4px;
  transition: background-color 0.3s ease;
}

.sidebar-button:hover, .sidebar a:hover {
  background-color: #c0c0c0;
}

.sidebar-section {
  font-weight: bold;
  margin-top: 10px;
  margin-bottom: 5px;
  padding-left: 8px;
}

.sidebar a.active, .sidebar-button.active {
  background-color: #a0a0a0;
  font-weight: bold;
  color: white;
}

.logout-button {
  background-color: #ff6b6b;
  border: none;
  padding: 10px;
  width: 100%;
  cursor: pointer;
  color: white;
  font-weight: bold;
  font-size: 14px;
  border-radius: 4px;
  transition: background-color 0.3s ease;
}

.logout-button:hover {
  background-color: #ff4c4c;
}

/* Responsive for smaller screens */
@media (max-width: 600px) {
  .sidebar {
    width: 100%;
    height: auto;
    box-shadow: none;
    border-right: none;
  }
  .sidebar nav ul ul {
    padding-left: 10px;
  }
}
